/*************************
Request Password Reset Styling
*************************/
.reset--password--section {
  background-image: linear-gradient(to top left, #a34be7, #a745e8, #ac3ee8, #9a17d6, #a910d8, #b32dea, #b22eec, #b02fed, #a73af0, #9d42f2, #944af4, #8a50f6);
  background-repeat: no-repeat;
  padding: 4rem 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: 92vh;
}
.reset--password--section form {
  padding: 2.5rem 1rem;
  background-color: var(--color-white);
  border-radius: 4px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.4);
  width: 90%;
  max-width: 720px;
  margin: 0 auto;
  color: var(--input-label-color);
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 0.35rem;
}
.reset--password--section form legend {
  line-height: 1rem;
  font-size: 1.3rem;
  font-weight: 600;
  font-family: var(--tertiary-font);
  color: var(--legend-color);
  margin-bottom: 0.75rem;
  text-decoration: 2px underline currentColor;
  text-underline-offset: 2px;
}

.form__group {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-flow: column wrap;
}
.form__group label {
  font-size: 0.95rem;
  font-weight: 600;
  line-height: 1.2rem;
}
.form__group input {
  font-family: var(--primary-font);
  font-size: 0.95rem;
  width: 100%;
  padding: 0.45rem 0.5rem;
  border: 1px solid var(--input-field-border);
  border-radius: 2px;
  outline: none;
  border-radius: 3px;
}
.form__group input:focus {
  outline: 1px solid var(--color-black);
}
.form__submit__group input {
  margin-top: 0.75rem;
  padding: 0.75rem 1.2rem;
}